.footer {
    background-color: rgba(0,0,0,0);
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.git-logo {
    height: 3em;
    background-color: rgba(0,0,0,0);
}

.git-logo:hover {
    border-radius: 5em;
    background-color: rgba(13, 226, 138, 0.541);
}